import request from '../axios'
const url = location.origin
export const getList = (params) =>
  request({
    url: url + '/bladeVisuals',
    method: 'get',
    params: params,
  })

export const copyObj = (id) =>
  request({
    url: url + '/bladeVisuals/copy',
    method: 'post',
    data: {
      id: id,
    },
  })

export const getCategory = (appId) =>
  request({
    url: url + '/reportTypes',
    method: 'get',
    params: {
      app_id: appId,
      type: '3',
    },
  })

export const getObj = (id) =>
  request({
    url: url + '/bladeVisuals/' + id,
    method: 'get',
  })

