export const addObj = (data) =>
  request({
    url: url + '/bladeVisuals',
    method: 'post',
    data: {
      category: data.category,
      status: data.status,
      title: data.title,
      appId: data.appId,
      authId: data.authId,
      authName: data.authName,
      detail: JSON.stringify({
        name: data.title,
        width: data.width,
        height: data.height,
        scale: 1,
        backgroundImage: '/img/bg/bg1.png',
        url: '',
        mark: {},
        gradeShow: false,
        gradeLen: 30,
      }),
      component: '[]',
    },
  })

export const updateComponent = (data) =>
  request({
    url: url + '/bladeVisuals',
    method: 'put',
    data: data,
  })

export const updateObj = (data) =>
  request({
    ///bladeVisuals/update/bladeVisual
    url: url + '/bladeVisuals/update/bladeVisual',
    //url: url + '/visual/update',
    method: 'post',
    data: {
      id: data.id,
      category: data.category,
      title: data.title,
      authId: data.authId,
    },
  })

export const delObj = (id) =>
  request({
    url: url + '/bladeVisuals',
    method: 'delete',
    data: {
      id: id,
    },
  })
