<template>
  <div class="split-pane custom-data-screen"
       slot="body">
    <split-pane :default-percent='20'
                :max-percent="40"
                split="vertical">
      <template slot="paneL">
        <el-card shadow="never"
                 class="split-pane__card left-card-menu">
          <el-row slot="header"
                  class="my-card-header">
            <!--<el-col :span="16" class="card-title-text text-align-left">流程定义</el-col>-->
            <el-col :span="24">
              <el-select v-model="currentAppId"
                         style="width: 100%"
                         placeholder="请选择应用"
                         @change="getReportTree()">
                <el-option v-for="item in appData"
                           :key="item.id"
                           :label="item.name"
                           :value="item.id">
                </el-option>
              </el-select>
            </el-col>
          </el-row>
          <el-scrollbar class="default-scrollbar"
                        wrap-class="default-scrollbar__wrap"
                        view-class="scrollbar__view">
            <el-tree :data="typelist"
                     ref="dataScreenTree"
                     :expand-on-click-node="false"
                     highlight-current
                     node-key="id"
                     :props="treeProps"
                     default-expand-all
                     @node-click="menuNodeClickHandle">
              <span class="custom-tree-node"
                    slot-scope="{ node, data }">
                <span class="custom-tree-node__title">
                  <i :class="'iconfont icon-folder-open'"></i>
                  {{ data.name }}
                </span>
                <!--<span>
                          <el-button v-if="data.type === 'parentNode'&&data.id!=='0'"
                                     class="success-text-btn"
                                     title="新增报表"
                                     type="text"
                                     @click="addReportHandle(data)"
                                     size="mini">
                              <i class="fa fa-plus"></i>
                          </el-button>
                          <el-button v-if="data.type === 'childNode'"
                                     class="info-text-btn"
                                     title="删除"
                                     @click="deleteReportHandler(data)"
                                     type="text"
                                     size="mini">
                                <i class="fa fa-minus"></i>
                          </el-button>
                        </span>-->
              </span>
            </el-tree>
          </el-scrollbar>
        </el-card>
      </template>
      <template slot="paneR">
        <list ref="list"
              :type-list="typelist"
              :app-id="currentAppId"></list>
      </template>
    </split-pane>
  </div>
  <!-- <div class="dataScreenList">
         <el-container class="list">
             <el-scrollbar class="list"
                           style="width:100%;height:800px;">
                 <maps v-if="activeName==2"></maps>
                 <category v-if="activeName==3"></category>
             </el-scrollbar>
         </el-container>
     </div>-->
</template>
<script>
import splitPane from 'vue-splitpane';
//src\page\list\list.vue
import list from './list/list'
//导入主题文件
import '../theme/index.js'
import { getCategory } from "../api/visual";

export default {
  name: "index",
  components: {
    list,
    splitPane
  },
  data () {
    return {
      appData: [],
      activeName: '1',
      currentAppId: '',
      typelist: [],
      categoryActiveName: '',
      appName: '',
      treeProps: {
        label: 'categoryKey',
      }
    }
  },
  created () {
    this.updateAppList('');

  },
  mounted () {
    //$('#acebtn').click()
  },
  methods: {
    //获取体表类型
    getReportTree () {
      //请求类别
      this.getCategory()
    },
    handleSelect (key) {
      this.activeName = key;
    },
    getCategory () {
      getCategory(this.currentAppId).then(({ data }) => {
        if (data.code === 40000) {
          this.typelist = data.list;
          this.categoryActiveName = (data[0] || {}).categoryValue;
          this.$refs.list.activeName = this.categoryActiveName;
          this.$refs.list.page.page = 1;
          if (this.typelist.length > 0) {
            this.$nextTick(() => {
              this.$refs.dataScreenTree.setCurrentKey(this.typelist[0].id)
              this.menuNodeClickHandle(this.typelist[0])
            })
          }
        } else {
          this.$message.error(data.msg);
        }
      })
    },
    //左侧菜单节点点击事件
    menuNodeClickHandle (data) {
      this.$refs.list.activeName = data.id;
      this.$refs.list.page.page = 1;
      this.$refs.list.getList(data.categoryValue)
    },
    updateAppList (orgId) {
      this.dataList = [];
      this.appDataLoading = true;
      this.$http({
        url: location.origin + '/sysGroupApps/list/userOrgApp',
        method: 'get',
        params: {
          'group_id': orgId
        }
      }).then((response) => {

        if (response.data && response.data.code === 40000) {
          this.appDataLoading = false;
          this.appData = response.data.list;
          if (response.data.list.length > 0) {
            this.currentAppId = response.data.list[0].id
            this.getCategory(this.currentAppId)
          } else {
            this.currentAppId = ''
          }
        } else {
          this.$message.error(response.data.msg)
        }
      })
    },
  }
}
</script>

