<template>
  <el-dialog :title="title"
             :close-on-click-modal="false"
             :visible.sync="visible"
             width="900px"
             append-to-body>
    <el-container v-loading="isLoadingData">
      <el-main>
        <el-row style="margin-bottom: 10px">
          <el-col class="text-align-right">
            <el-input placeholder="权限名称"
                      v-model="dataForm.name"
                      style="width: 30%"
                      size="small"
                      clearable>
              <el-button @click="getDataList()"
                         slot="append"
                         icon="el-icon-search"></el-button>
            </el-input>
          </el-col>
        </el-row>
        <el-table border
                  :data="dataList"
                  height="500px"
                  row-key="id"
                  :highlight-current-row="highlightFlg"
                  :default-expand-all="true"
                  @row-dblclick="rowDblClickHandler"
                  @row-click="rowClickHandler">
          <el-table-column prop="id"
                           v-if="false">
          </el-table-column>
          <el-table-column header-align="left"
                           align="left"
                           label="权限名称"
                           show-overflow-tooltip
                           width="400">
            <template slot-scope="scope">
              <p class="key-name">
                {{ scope.row.name }}
              </p>
            </template>
          </el-table-column>
          <el-table-column prop="code"
                           label="权限标识"
                           header-align="center"
                           align="center"
                           show-overflow-tooltip>
          </el-table-column>
        </el-table>
      </el-main>
      <el-footer>
        <div style="float: right;">
          <el-button type="primary"
                     @click="selectHandler"
                     :disabled="selection===null">确定</el-button>
          <el-button @click="visible = false">取消</el-button>
        </div>
      </el-footer>
    </el-container>
  </el-dialog>
</template>

<script>
export default {
  name: "selectAuth",
  props: {
    title: {
      type: String,
      default: "请选择权限",
    },
  },
  data () {
    return {
      visible: false,
      dataList: [],
      isLoadingData: false,
      selection: null,
      highlightFlg: false,
      appId: '',
      dataForm: {
        name: ''
      }
    }
  },
  methods: {
    showDialog (appId) {
      this.highlightFlg = false
      this.selection = null
      this.appId = appId
      this.visible = true
      this.dataForm.name = ''
      this.getDataList()
    },
    getDataList () {
      this.isLoadingData = true
      this.$http({
        url: '/sysAuths',
        method: 'get',
        params: {
          'appId': this.appId,
          'name': this.dataForm.name
        }
      }).then(({ data }) => {
        if (data && data.code === 40000) {
          this.dataList = data.list
        } else {
          this.dataList = []
        }
        this.isLoadingData = false
      })
    },
    selectHandler () {
      this.$emit('submit', this.selection);
      this.visible = false;
    },
    rowDblClickHandler (row) {
      this.highlightFlg = true
      this.selection = row
      this.selectHandler()
    },
    rowClickHandler (row) {
      this.highlightFlg = true
      this.selection = row
    }
  }
}
</script>

