<template>
  <el-container class="list">
    <!--<el-header>
          <el-menu :default-active="activeName"
                   mode="horizontal"
                   @select="handleSelect">
            <el-menu-item :index="item.categoryValue"
                          :key="item.categoryValue"
                          v-for="item in typelist"
                          @click="getList(item.categoryValue)">
              <i class="iconfont icon-daping"></i>
              {{item.categoryKey}}
            </el-menu-item>
          </el-menu>
        </el-header>-->
    <div>
      <el-main>
        <div class="content">
          <div class="content__item content__item--add"
               @click="handleAdd">
            <div>
              <i class="el-icon-plus"></i>
              <p>新建大屏</p>
            </div>
          </div>
          <div class="content__item"
               v-for="(item,index) in list"
               :key="index"
               @mouseover="item._menu=true"
               @mouseout="item._menu=false">
            <div class="content__info">
              <img v-if="item.backgroundInfo"
                   :src="item.backgroundInfo"
                   alt="" />
              <div class="content__menu"
                   v-show="item._menu">
                <div class="content__btn"
                     @click="handleEdit(item)">
                  设计
                </div>
              </div>
            </div>
            <div class="content__main">
              <span class="content__name">{{item.title}}</span>
              <div class="content__menulist">
                <div class="content__view">
                  <el-tooltip content="删除">
                    <i class="el-icon-delete"
                       @click="handleDel(item,index)"></i>
                  </el-tooltip>
                  <el-tooltip content="编辑">
                    <i class="el-icon-edit"
                       @click="handleUpdate(item,index)"></i>
                  </el-tooltip>
                  <el-tooltip content="预览">
                    <i class="el-icon-view"
                       @click="handleViews(item,index)"></i>
                  </el-tooltip>
                  <el-tooltip content="复制">
                    <i class="el-icon-copy-document"
                       @click="handleCopy(item,index)"></i>
                  </el-tooltip>
                </div>
                <span class="content__status"
                      :class="{'content__status--active':item.status}">

                </span>
              </div>

            </div>
          </div>
        </div>
        <div class="page">
          <el-pagination layout="total, sizes, prev, pager, next, jumper"
                         background
                         @size-change="handleSizeChange"
                         @current-change="handleCurrentChange"
                         :page-size="page.size"
                         :current-page.sync="page.page"
                         :total="page.total">
          </el-pagination>
        </div>
      </el-main>
      <el-dialog :title="dialogTitle"
                 width="35%"
                 :visible.sync="box">
        <el-form size="mini"
                 label-width="100px">
          <el-row>
            <el-col style="padding: 0 10px">
              <el-form-item label="分组">
                <el-select v-model="form.category"
                           placeholder="请选择分组">
                  <el-option v-for="item in typeList"
                             :label="item.name"
                             :value="item.id"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="关联权限"
                            prop="authName">
                <el-input v-model="form.authName"
                          placeholder="关联权限"
                          :readonly="true">
                  <el-button slot="prepend"
                             title="请选择"
                             @click="selectAuthHandler"
                             icon="el-icon-search" />
                  <el-button slot="append"
                             title="清空"
                             @click="removeQueryAuthNameHandler"
                             icon="el-icon-remove-outline" />
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <avue-form :option="option"
                   v-model="form"
                   @submit="handleSave"></avue-form>

      </el-dialog>
    </div>
    <select-Auth ref="selectAuth"
                 @submit="getAuthInfo"></select-Auth>

  </el-container>
</template>
<script>
import { getList, addObj, updateObj, delObj, getCategory, copyObj } from '../../api/visual';
import selectAuth from '@/components/selectAuth.vue';
export default {
  name: "list",
  props: {
    typeList: {
      type: Array,
      // eslint-disable-next-line vue/require-valid-default-prop
      default: []
    },
    appId: {
      type: String,
      default: ''
    }
  },
  components: {
    selectAuth
  },
  data () {
    return {
      typelist: [],
      index: 0,
      type: '',
      dialogTitle: '新建大屏',
      option: {
        column: [{
          label: '大屏名称',
          span: 24,
          labelWidth: 100,
          prop: 'title',
          rules: [{
            required: true,
            message: "请输入大屏名称",
            trigger: "blur"
          }]
        }, {
          label: '大屏尺寸',
          span: 14,
          labelWidth: 100,
          prop: 'width',
          placeholder: '请输入宽度',
          rules: [{
            required: true,
            message: "请输入大屏尺寸",
            trigger: "blur"
          }]
        }, {

          label: '',
          span: 10,
          labelWidth: 1,
          prop: 'height',
          placeholder: '请输入高度',
          rules: [{
            required: true,
            message: "请输入大屏尺寸",
            trigger: "blur"
          }]
        }]
      },
      page: {
        page: 1,
        size: 10,
        total: 0,
      },
      form: {},
      box: false,
      activeName: '',
      list: [],
      appName: '',
    }
  },
  created () {
  },
  methods: {
    selectAuthHandler () {
      this.$nextTick(() => {
        this.$refs.selectAuth.showDialog(this.appId)
      })
    },
    removeQueryAuthNameHandler () {
      this.form.authId = '';
      this.form.authName = '';
    },
    getAuthInfo (selection) {
      if (selection === null) return;
      this.form.authId = selection.id;
      this.form.authName = selection.name
    },
    vaildData (id) {
      let num = Number(id);

      if (num) {
        return true
      }
      return false

      /*const list = [];
      for (var i = 0; i < 17; i++) {
        list.push(i)
      }
      return list.includes(id)*/
    },

    getCategory () {
      getCategory().then(res => {
        const data = res.data.data;
        this.typelist = data;
        this.activeName = (data[0] || {}).categoryValue;
      })
    },
    handleCopy (item) {
      this.$confirm('确认复制当前大屏', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        copyObj(item.id).then(({ data }) => {
          if (data.code === 40000) {
            this.$message.success('复制成功');
            this.getList();
          } else {
            this.$message.error(data.msg)
          }
        }).catch(() => {
          this.$message.error('请求失败，请查看控制台或者联系管理员。')
        })
      })
    },
    handleDel (item, index) {
      this.$confirm('是否确认永久删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // if (this.vaildData(item.id)) {
        //   this.$message.error('例子模板不允许修改')
        //   return false;
        // }
        delObj(item.id).then(({ data }) => {
          if (data.code === 40000) {
            this.list.splice(index, 1);
            this.$message.success(data.msg)
          } else {
            this.$message.error(data.msg)
          }

        })
      }).catch(() => {

      });
    },

    handleUpdate (item, index) {
      this.form = item;
      this.form.category = this.activeName;
      this.form.authId = item.authId ? item.authId : '';
      this.form.authName = item.authId && item.authName ? item.authName : '';
      //this.form.category = this.form.category + '';
      this.type = 'edit';
      this.option.column[1].display = false;
      this.option.column[2].display = false;
      /*  this.option.column[2].display = false;
        this.option.column[3].display = false;*/
      this.box = true;
      this.index = index;
    },
    handleEdit (item) {
      console.log(345, location)
      // let routeUrl = this.$router.resolve({
      //   path: 
      // });


      window.open(location.href + 'build/' + item.id + '?appId=' + this.appId, '_blank');
      // this.$router.push({
      //   path: '/build/' + item.id + '?appId=' + this.appId
      // })
    },
    handleViews (item) {
      let routeUrl = this.$router.resolve({
        path: '/view/' + item.id
      })
      window.open(routeUrl.href, '_blank');
    },
    handleAdd () {
      this.type = 'add';
      //this.option.column[5].display = false;
      this.form.category = this.activeName;
      this.form.title = '';
      this.form.width = 1920;
      this.form.height = 1080;
      this.form.authId = '';
      this.form.authName = '';
      this.box = true;
    },
    handleSave (form, done) {
      done();
      if (this.type == 'add') {
        let objA = Object.assign({
          category: this.activeName,
          appId: this.appId,
          backgroundInfo: '/img/bg/bg12.jpg',
          isDeleted: 0
        }, this.form);
        addObj(objA).then(({ data }) => {
          if (data.code === 40000) {
            this.box = false;
            this.$message.success('新增成功');
            this.getList();
            //const id = res.data.data.id;
            //this.handleEdit({ id })
          } else {
            this.$message.error(data.msg);
          }

        }).catch((err) => {
          console.log(err)
        })
      } else {
        updateObj(Object.assign({
          category: this.form.category
        }, {
          id: this.form.id,
          status: this.form.status,
          authId: this.form.authId,
          title: this.form.title
        })).then(() => {
          this.box = false;
          this.$message.success('修改成功');
          this.getList();
        })
      }
    },
    handleSelect (key) {
      this.activeName = key;
      this.page.page = 1;
      this.getList();
    },
    handleCurrentChange (val) {
      this.page.page = val;
      this.getList();
    },
    handleSizeChange (val) {
      this.page.size = val;
      this.getList();
    },
    getList (category) {
      const loading = this.$loading({
        lock: true,
        text: '',
        spinner: '',
        background: 'rgba(0, 0, 0, 0.5)'
      });
      this.list = [];
      getList({
        category: category || this.activeName,
        current: this.page.page,
        size: this.page.size,
      }).then(({ data }) => {
        if (data.code === 40000) {
          loading.close();
          this.page.total = data.page.total;
          this.list = data.page.records;
          this.initData();
        } else {
          this.$message.error(data.msg);
        }
      })
    },
    initData () {
      this.list.forEach((ele, index) => {
        this.$set(this.list[index], '_menu', false)
      })
    }
  }
}
</script>
<style lang="scss">
@import "../../styles/list.scss";
</style>
<style lang="scss" scoped>
@import "@/styles/mixi.scss";
.nodata {
  width: 100%;
  height: 100%;
  text-align: center;
  color: $light--color--text-6;
  font-size: 16px;
  padding-top: 20%;
  box-sizing: border-box;
}
</style>
